body {
	font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen-Sans,
		Ubuntu, Cantarell, Helvetica Neue, sans-serif;
}

.container {
	max-width: 1200px;
	margin: 0 auto;
}

a {
	text-decoration: none;
	color: #d0021b;
}

ul {
	padding: 0;
}

li {
	list-style-type: none;
}

.button {
	color: #e6e6e6;
	background: #0a0a0a;
	border: none;
	font-size: 16px;
	border-radius: 3px;
	width: 200px;
	text-align: center;
	display: block;
	padding: 7px 0;
	margin: 10px auto;
}

.button:hover:enabled {
	background: linear-gradient(#1a1a1a, #0a0a0a);
	color: #fff;
	text-decoration: none;
}

.button:active {
	transform: translateY(1px);
}

.avatar {
	width: 150px;
	border-radius: 50%;
}

.space-list-items {
	margin-bottom: 7px;
}

.languages {
	display: flex;
	justify-content: center;
}

.languages li {
	margin: 10px;
	font-weight: bold;
	cursor: pointer;
}

.popular-list {
	display: flex;
	flex-wrap: wrap;
	justify-content: space-around;
}

.popular-item {
	margin: 20px;
	text-align: center;
}

.popular-rank {
	font-size: 20px;
	margin: 10px;
}

.home-container {
	display: flex;
	flex-direction: column;
	align-items: center;
}

.nav {
	display: flex;
}

.nav li {
	margin-right: 15px;
}

.active {
	font-weight: bold;
}

.row {
	display: flex;
	justify-content: space-around;
}

.column {
	display: flex;
	flex-direction: column;
	width: 500px;
	align-items: center;
}

.column input {
	border-radius: 3px;
	margin: 10px 0;
	padding: 5px;
	border: 1px solid rgba(0, 0, 0, 0.43);
	font-size: 16px;
	width: 80%;
}

.column label {
	text-align: center;
	font-size: 30px;
	font-weight: 200;
}

.header {
	text-align: center;
	font-size: 30px;
	font-weight: 200;
}

.reset {
	border: none;
	background: transparent;
	color: #d0021b;
	display: block;
	width: 100%;
	text-align: center;
	outline: none;
}

.reset:hover {
	cursor: pointer;
}

.loader-container {
	display: flex;
	height: auto;
	width: 100%;
	margin-top: 200px;
	justify-content: center;
	align-items: center;
}

/* Loader spinner */

.loader {
	width: 48px;
	height: 48px;
	border: 5px solid;
	border-color: #d0021b transparent;
	border-radius: 50%;
	display: inline-block;
	box-sizing: border-box;
	animation: rotation 1s linear infinite;
}

@keyframes rotation {
	0% {
		transform: rotate(0deg);
	}
	100% {
		transform: rotate(360deg);
	}
}
